<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b></b></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>
    <el-col :span="24" class="warp-main"
            v-loading="loading"
            element-loading-text=""
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.8)">
      <!--  -->
      <el-col :span="24" class="toolbar">
        <el-form :inline="true" class="demo-form-inline" >
            <el-input :placeholder="placeholder" v-model="keywords" style="width: 30%;">
              <el-select class="sel-placeholder" v-model="select" @change="searchFieldChange" slot="prepend" style="width:130px">
                <el-option label="" value="name"></el-option>
                <el-option label="ID" value="adminId"></el-option>
                <el-option label="" value="adminName"></el-option>
              </el-select>
              <el-button slot="append" icon="el-icon-search" @click="getStores"></el-button>
            </el-input>
          <template v-if="userInfo.role === 'ADMIN'">
            <el-button type="info" plain icon="el-icon-plus" @click="dialogCreateVisible = true"></el-button>
          </template>
        </el-form>
      </el-col>

      <!-- -->
      <el-table :data="stores"
                style="width: 100%"
                height="680"
                ref="table"
                :default-sort = "{prop: 'bailPercentage', order: 'descending'}"
                @selection-change="tableSelectionChange">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="">
                <span>{{ props.row.adminPwd }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column prop="name" label="" align="center">
          <template slot-scope="scope">
            <el-button @click="viewDetail(scope.row)" type="text" size="small">{{scope.row.name}}</el-button>
          </template>
        </el-table-column>
        <el-table-column prop="adminId" label="ID" align="center"></el-table-column>
        <el-table-column prop="adminName" label="" align="center"></el-table-column>
        <el-table-column prop="adminPwd" label="" align="center">
          <template slot-scope="scope">
            <el-button
              size="mini" type="info" plain
              @click="viewPwd(scope.row)">
            </el-button>
          </template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button
              size="mini" type="info" plain
              @click="viewChannel(scope.row)">
            </el-button>
          </template>
        </el-table-column>
        <el-table-column prop="channelType" label="" align="center"></el-table-column>
        <template v-if="this.userInfo.role === 'ADMIN'">
          <el-table-column label="" align="center">
            <template slot-scope="scope">
              <el-button
                size="mini" type="danger" plain
                @click="handleChangePwd(scope.row)">
              </el-button>
            </template>
          </el-table-column>
        </template>
      </el-table>

      <template v-if="userInfo.role === 'ADMIN'">
        <!-- -->
        <el-dialog title="" center v-model="dialogCreateVisible" :visible.sync="dialogCreateVisible" :close-on-click-modal="false" @close="reset" >
          <el-steps :active="steps.active" :finish-status="steps.finishStatus" align-center>
            <el-step title="1" description=""></el-step>
            <el-step title="2" description=""></el-step>
            <el-step title="3" description=""></el-step>
          </el-steps>
          <br>
          <template v-if="steps.active === 0">
            <!-- step 1-->
            <el-form id="#createStore" :model="createStore" :rules="createStoreRules" ref="createStore" label-width="120px">
              <el-form-item label="" prop="name">
                <el-input v-model="createStore.name"></el-input>
              </el-form-item>
              <el-form-item label="" prop="channelType">
                <el-select v-model="createStore.channelType" placeholder="">
                  <el-option label="" value="WECHAT"></el-option>
                  <el-option label="" value="ALIPAY"></el-option>
                  <el-option label="H5" value="H5"></el-option>
                  <el-option label="APP" value="APP"></el-option>
                  <el-option label="" value="BANK"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" prop="bailPercentage">
                <el-input v-model="createStore.bailPercentage"></el-input>
              </el-form-item>
              <el-form-item label="" prop="adminName">
                <el-input v-model="createStore.adminName"></el-input>
              </el-form-item>
              <el-form-item label="" prop="csrTel">
                <el-input v-model="createStore.csrTel"></el-input>
              </el-form-item>
              <el-form-item label="" prop="agent">
                <el-select v-model="createStore.agentId" placeholder="">
                  <el-option
                    v-for="item in agentsInfo"
                    :key="item[0]"
                    :label="item[3]"
                    :value="item[0]">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-form>
          </template>
          <template v-if="steps.active === 1">
            <!-- step 2-->
            <el-form ref="createAdmin" :model="createAdmin" :rules="createAdminRules"  label-width="100px">
              <el-form-item prop="account" label="">
                <el-input v-model="createAdmin.account"></el-input>
              </el-form-item>
              <el-form-item prop="password" label="">
                <el-input type="password" v-model="createAdmin.password" placeholder="123456"></el-input>
              </el-form-item>
              <el-form-item prop="name" label="">
                <el-input v-model="createAdmin.name"></el-input>
              </el-form-item>
              <el-form-item prop="agent" label="">
                <el-select v-model="createAdmin.agentId" @change="onChange" placeholder="">
                  <el-option
                    v-for="item in agentsInfo"
                    :key="item[0]"
                    :label="item[3]"
                    :value="item[0]">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item prop="store" label="">
                <el-select v-model="createAdmin.storeId" placeholder="">
                  <el-option
                    v-for="item in tempStoresInfo"
                    :key="item[0]"
                    :label="item[1]"
                    :value="item[0]">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item prop="role" label="">
                <el-select v-model="createAdmin.role" placeholder="">
                  <el-option label="" value="AGENT"></el-option>
                  <el-option label="" value="STORE"></el-option>
                </el-select>
              </el-form-item>
            </el-form>
          </template>
          <template v-if="steps.active === 2">
            <!-- step 3-->
            <el-form id="#create" :model="createChannel" :rules="createChannelRules" ref="createChannel" label-width="120px">
              <el-form-item label="ID" prop="extStoreId">
                <el-input v-model="createChannel.extStoreId"></el-input>
              </el-form-item>
              <el-form-item label="" prop="extStoreName">
                <el-input v-model="createChannel.extStoreName"></el-input>
              </el-form-item>
              <el-form-item label="" prop="paymentGateway">
                <el-select v-model="createChannel.paymentGateway" placeholder="">
                  <el-option label="H5" value="CHINAUMSH5"></el-option>
                  <el-option label="APP" value="CHINAUMSAPP"></el-option>
                  <el-option label="" value="IPS"></el-option>
                </el-select>
              </el-form-item>
              <template v-if="createChannel.paymentGateway === 'CHINAUMSH5'||createChannel.paymentGateway === 'CHINAUMSAPP'">
                <el-form-item label="" prop="tid">
                  <el-input v-model="createChannel.chinaUmsProps.tid"></el-input>
                </el-form-item>
                <el-form-item label="ID" prop="msgSrcId">
                  <el-input v-model="createChannel.chinaUmsProps.msgSrcId"></el-input>
                </el-form-item>
                <el-form-item label="" prop="msgSrc">
                  <el-input v-model="createChannel.chinaUmsProps.msgSrc"></el-input>
                </el-form-item>
                <el-form-item label="" prop="signKey">
                  <el-input v-model="createChannel.chinaUmsProps.signKey"></el-input>
                </el-form-item>
              </template>
              <el-form-item label="" prop="InstMid">
                <el-input v-model="createChannel.InstMid"></el-input>
              </el-form-item>
              <!--<el-form-item label="" prop="agent">
                <el-select v-model="createChannel.agent" placeholder="">
                  <el-option
                    v-for="item in agentsInfo"
                    :key="item[0]"
                    :label="item[3]"
                    :value="item[0]">
                  </el-option>
                </el-select>
              </el-form-item>-->
            </el-form>
          </template>
          <div slot="footer" class="dialog-footer">
            <el-button plain @click="dialogCreateVisible = false"> </el-button>
            <el-button type="primary" plain :loading="createLoading" @click="next"></el-button>
          </div>
        </el-dialog>

        <el-dialog title="" v-model="dialogChangePwdVisible" :visible.sync="dialogChangePwdVisible" :close-on-click-modal="false">
          <el-form ref="formEdit" :model="formEdit" label-width="100px">
            <el-form-item label="">
              <el-input v-model="formEdit.name" disabled></el-input>
            </el-form-item>
            <el-form-item prop="account" label="">
              <el-input v-model="formEdit.account" disabled></el-input>
            </el-form-item>
            <el-form-item prop="password" label="">
              <el-input v-model="formEdit.password"></el-input>
            </el-form-item>
            <el-form-item prop="role" label="">
              <el-select v-model="formEdit.role" placeholder="">
                <el-option label="" value="AGENT"></el-option>
                <el-option label="" value="STORE"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary"></el-button>
            </el-form-item>
          </el-form>
        </el-dialog>
      </template>

      <el-pagination class="paging"
                     :current-page="filter.currentPage"
                     :page-sizes="[10, 20, 50, 100]"
                     :page-size="filter.pageSize"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="totalRows"
                     @size-change="pageSizeChange"
                     @current-change="pageCurrentChange">
      </el-pagination>
    </el-col>
  </el-row>
</template>

<script>
  let placeholders = {
    "name":"",
    "adminId":"ID",
    "adminName":"",
  };

  export default {
    data: function() {
      return {
        userInfo:{
          id:'',
          account:'',
          name:'',
          role:''
        },
        agentsInfo:{},
        stores: [],
        update:{
          name: '',
          appId: '',
          bailPercentage: '',
          code: '',
          csrTel: '',
          proxyUrl: '',
          dailyLimit: ''
        },
        steps: {
          active: 0,
          finishStatus: 'success'
        },
        createStore: {
          name: '',
          channelType: '',
          bailPercentage: '',
          code: '',
          csrTel: '',
          adminName: '',
          agentId: ''
        },
        createAdmin: {
          account: '',
          password: '123456',
          name: '',
          agentId: '',
          storeId: '',
          role: ''
        },
        createChannel: {
          extStoreId: "",
          extStoreName: "",
          paymentGateway: "",
          chinaUmsProps: {
            tid: "",
            msgSrcId: "",
            msgSrc: "",
            signKey: ""
          },
          InstMid: ""
        },
        tempStoresInfo:{},
        formEdit: {
          account: '',
          password: '',
          name: '',
          role: ''
        },
        createAdminRules: {
          account: [
            { required: true, message: '', trigger: 'blur' },
          ],
          password: [
            { required: true, message: '', trigger: 'blur' },
          ],
          name: [
            { required: true, message: '', trigger: 'blur' },
          ],
          role: [
            { required: true, message: '', trigger: 'blur' },
          ]
        },
        createStoreRules: {
          name: [
            { required: true, message: '', trigger: 'blur' },
          ],
          channelType: [
            { required: true, message: '', trigger: 'blur' },
          ],
          bailPercentage: [
            { required: true, message: '', trigger: 'blur' },
          ],
          csrTel: [
            { required: true, message: '', trigger: 'blur' },
          ],
          adminName: [
            { required: true, message: '', trigger: 'blur' },
          ]
        },
        createChannelRules: {
          extStoreId: [
            { required: true, message: 'ID', trigger: 'blur' },
          ],
          extStoreName: [
            { required: true, message: '', trigger: 'blur' },
          ],
          paymentGateway: [
            { required: true, message: '', trigger: 'blur' },
          ],
          tid: [
            { required: true, message: '', trigger: 'blur' },
          ],
          msgSrcId: [
            { required: true, message: 'ID', trigger: 'blur' },
          ],
          msgSrc: [
            { required: true, message: '', trigger: 'blur' },
          ],
          signKey: [
            { required: true, message: '', trigger: 'blur' },
          ],
          agent: [
            { required: true, message: '', trigger: 'blur' },
          ]
        },
        filter: {
          pageSize: 10,
          currentPage: 1,
          beginIndex: 0,
          name: '',
          bailPercentage: '',
          code: '',
          csrTel: '',
          proxyUrl: ''
        },
        totalRows: 0,
        keywords: '', //
        select: 'name', //
        loading: true,
        selected: [], //
        dialogCreateVisible: false, //
        dialogUpdateVisible: false, //
        dialogChangePwdVisible: false,
        createLoading: false,
        updateLoading: false,
        placeholder:placeholders["name"]
      };
    },
    mounted: function() {
      this.agentsInfo = JSON.parse(sessionStorage.getItem('agentsInfo'));
      console.log(this.agentsInfo);
      if(this.createAdmin.agentId === ''){
        this.tempStoresInfo = JSON.parse(sessionStorage.getItem('storesInfo'));
        console.log(this.tempStoresInfo);
      }

      let userInfo = sessionStorage.getItem('access-user');
      if (userInfo) {
        userInfo = JSON.parse(userInfo);
        this.userInfo.id = userInfo.id;
        this.userInfo.account = userInfo.account;
        this.userInfo.name = userInfo.name;
        this.userInfo.role = userInfo.role;
      }

      this.getStores()
    },
    methods: {
      tableSelectionChange(val) {
        this.selected = val;
      },
      searchFieldChange(val) {
        this.select = val;
        this.placeholder=placeholders[val];
        console.log(` ${val} `);
      },
      pageSizeChange(val) {
        console.log(` ${val} `);
        this.filter.pageSize = val;
        this.getStores();
      },
      pageCurrentChange(val) {
        console.log(`: ${val}`);
        this.filter.currentPage = val;
        this.getStores();
      },
      // 
      reset() {
        this.steps.active = 0;
      },
      //
      getStores() {
        this.loading = true;
        this.$http.get(`http://www.wfpay.xyz/xpay/admin/${this.userInfo.id}/stores`).then(res => {
          if (res.data.data) {
            res.data.data.forEach(val =>{
              if(val.channelType === 'WECHAT'){
                val.channelType = ''
              }
              if(val.channelType === 'ALIPAY'){
                val.channelType = ''
              }
              if(val.channelType === 'BANK'){
                val.channelType = ''
              }
            });
            this.stores = res.data.data;
          }
          //
          let queryData = [];
          if(this.keywords !==""){
            for (var i=0,lenI=this.stores.length;i<lenI;i++) {
              let reg = new RegExp(this.keywords);
              if(this.stores[i][this.select].toString().match(reg)){
                queryData.push(this.stores[i]);
              }
            }
          }
          else queryData = this.stores;
          this.totalRows = queryData.length;
          //
          this.filter.beginIndex = (this.filter.currentPage-1)*10;
          this.stores = queryData.splice(this.filter.beginIndex,this.filter.pageSize);
          this.loading = false;
          this.selected.splice(0,this.selected.length);
        })
      },
      viewPwd(row){
        this.$refs.table.toggleRowExpansion(row);
      },
      viewDetail(row){
        this.$router.push({ path: '/store/storeDetails' });
      },
      viewChannel(row){
        if(row.channels){
          sessionStorage.setItem('channelData', JSON.stringify(row.channels));
          this.$router.push({ path: '/store/storeChannels' });
        }
        else {
          this.$message.error('')
        }
      },
      // 
      /*onChange() {
        let storesInfo = JSON.parse(sessionStorage.getItem('storesInfo'));
        this.tempStoresInfo = storesInfo.filter(info => {
          if(this.formCreate.agentId === info[3]){
            return info;
          }
        });
        console.log(this.tempStoresInfo);
      },*/
      next(){
        if(this.steps.active === 0){
          this.handleCreateStore();
          return true
        }
        if(this.steps.active === 1){
          this.handleCreateAdmin();
          return true
        }
        if(this.steps.active === 2){
          this.handleCreateChannel()
        }
      },
      handleCreateStore(){
        this.steps.active ++;
        /*this.$refs.createStore.validate((valid) => {
          if (valid) {
            this.createLoading = true;
            this.$http.put(`http://www.wfpay.xyz/xpay/admin/${this.userInfo.id}/stores`,this.createStore).then(res => {
              this.$message.success('');
              this.createLoading = false;
              this.$refs.createStore.resetFields();
              this.steps.active ++;
            }).catch(() =>{
              this.$message.error('');
              this.$refs.createStore.resetFields();
              this.createLoading = false;
            })
          }
          else {
            return false;
          }
        });*/
      },
      handleCreateAdmin(){
        this.steps.active ++;
        /*this.$refs.createAdmin.validate((valid) => {
          if (valid) {
            let id = JSON.parse(sessionStorage.getItem('access-user')).id;
            this.$http.put(`http://www.wfpay.xyz/xpay/admin/${id}/`,this.createAdmin).then(() => {
              this.$message.success('');
              this.$refs.createAdmin.resetFields();
              this.steps.active ++
            }).catch(() => {
              this.$message.error('');
              this.$refs.createAdmin.resetFields();
            })
          }
          else {
            return false;
          }
        })*/
      },
      handleCreateChannel(){
        /*this.$http.get(`http://www.wfpay.xyz/xpay/admin/${this.userInfo.id}/channels`).then(() => {
          this.$message.success('');
          this.$refs.CreateChannel.resetFields();
          this.reset();
          this.dialogCreateVisible = false
        }).catch(() => {
          this.$message.error('');
          this.$refs.CreateChannel.resetFields();
        });*/
        this.reset();
        this.dialogCreateVisible = false
      },
      handleChangePwd(row) {
        this.dialogChangePwdVisible = true;
        this.formEdit.name = row.name;
        this.formEdit.account = row.account;
      },

    }
  }
</script>

<style>
  .paging{
    text-align: center;
    margin:12px 0;
  }
  .btn-edit{
    float: right;
  }

</style>



