import Vue from 'vue'
import Router from 'vue-router'
import { getToken, setToken, removeToken } from '@/utils/auth'
import Home from '@/components/Home'
import Dashboard from '@/components/Dashboard'

import CreateAdmin from '@/components/system/CreateAdmin'
import EditAdmin from '@/components/system/EditAdmin'
import IncreaseQuota from '@/components/system/IncreaseQuota'
import Channels from '@/components/Channels'
import AgentList from '@/components/AgentList'
import Recharge from '@/components/Recharge/Recharge'
import RechargeList from '@/components/Recharge/RechargeList'
import StoreAdmins from '@/components/stores/StoreAdmins'
import StoreDetails from '@/components/stores/StoreDetails'
import StoreChannels from '@/components/stores/StoreChannels'
import AppList from '@/components/stores/AppList'
import DomainName from '@/components/stores/DomainName'
import Orders from '@/components/Orders'
import Complain from '@/components/Complain'

// 
const Login = resolve => require(['@/components/Login'], resolve);

Vue.use(Router);

let router = new Router({
  routes: [
    {
      path: '/login',
      name: '',
      component: Login,
    },
    {
      path: '/',
      name: 'home',
      component: Home,
      redirect: '/dashboard',
      leaf: true, // 
      menuShow: true,
      iconCls: 'iconfont icon-home', // class
      children: [
        {path: '/dashboard', component: Dashboard, name: '', menuShow: true}
      ]
    },
    /*{
      path: '/',
      component: Home,
      name: '',
      meta: { role: 'ADMIN'},
      iconCls: 'iconfont icon-users',
      menuShow: true,
      children: [
        {path: '/system/createAdmin', component: CreateAdmin, name: '', menuShow: true},
        {path: '/system/editAdmin', component: EditAdmin, name: '', menuShow: true},
        {path: '/system/increaseQuota', component: IncreaseQuota, name: '', menuShow: true}
      ]
    },*/
    {
      path: '/',
      component: Home,
      name: '',
      meta: { role:['ADMIN','AGENT'] },
      iconCls: 'iconfont icon-users1',
      menuShow: true,
      children: [
        {path: '/store/storeAdmins', component: StoreAdmins, name: '', menuShow: true},
        {path: '/store/storeDetails', component: StoreDetails, name: ''},
        {path: '/store/storeChannels', component: StoreChannels, name: ''}
      ]
    },
    {
      path: '/',
      component: Home,
      meta: { role: 'ADMIN'},
      iconCls: 'iconfont icon-users',
      leaf: true,
      menuShow: true,
      children: [
        {path: '/agent/agentList', component: AgentList, name: '', menuShow: true}
      ]
    },

/*{
  path: '/',
  component: Home,
  meta: { role:['ADMIN','AGENT']},
  iconCls: 'iconfont icon-users',
  leaf: true,
  menuShow: true,
  children: [
    {path: '/agent/AppList', component: AppList, name: 'App', menuShow: true}
  ]
},
{
  path: '/',
  component: Home,
  name: '',
  iconCls: 'iconfont icon-users',
  leaf: true,
  menuShow: true,
  children: [
    {path: '/channel/channelList', component: Channels, name: '', menuShow: true}
  ]
},
{
 path: '/',
 component: Home,
 meta: { role: 'STORE'},
 iconCls: 'iconfont icon-users',
 leaf: true,
 menuShow: true,
 children: [
   {path: '/recharge/rechargeOrder', component: Recharge, name: '', menuShow: true}
 ]
},*/
    {
      path: '/',
      component: Home,
      iconCls: 'iconfont icon-home1',
      leaf: true,
      menuShow: true,
      children: [
        {path: '/store/domainName', component: DomainName, name: '', menuShow: true},
      ]
    },
    {
      path: '/',
      component: Home,
      iconCls: 'iconfont icon-home1',
      leaf: true,
      menuShow: true,
      children: [
        {path: '/orders', component: Orders, name: '', menuShow: true},
      ]
    },
    {
      path: '/',
      component: Home,
      iconCls: 'iconfont icon-users',
      leaf: true,
      menuShow: true,
      children: [
        {path: '/recharge/rechargeList', component: RechargeList, name: '', menuShow: true}
      ]
    },
    {
      path: '/',
      component: Home,
      meta: { role: 'ADMIN'},
      iconCls: 'iconfont icon-leaf',
      leaf: true,
      menuShow: true,
      children: [
        {path: '/complain', component: Complain, name: '', menuShow: true},
      ]
    }
  ]
});

router.beforeEach((to, from, next) => {
  // console.log('to:' + to.path)
  if (to.path.startsWith('/login')) {
    //window.sessionStorage.removeItem('access-user')
    next()
  } else {
    let token = getToken();
    let accessUser = sessionStorage.getItem('access-user');
    if (!token||!accessUser) {
      next({path: '/login'})
    } else {
      next()
    }
  }
});

export default router



